<template>
    <div class="wrap_all">
        <div class="login-wrap" v-show="showLogin">
            <h3>登录</h3>
            <input type="text" placeholder="请输入用户名" v-model="username">
            <input type="password" placeholder="请输入密码" v-model="password">
            <button v-on:click="login">登录</button>
            <p><span v-on:click="ToRegister">没有账号？马上注册</span></p>
        </div>

        <div class="register-wrap" v-show="showRegister">
            <h3>注册</h3>
            <input type="text" placeholder="请输入用户名" v-model="newUsername">
            <input type="password" placeholder="请输入密码" v-model="newPassword">
            <button v-on:click="register">注册</button>
           <p><span v-on:click="ToLogin">已有账号？马上登录</span></p>
        </div>
    </div>
</template>

<style>
    .wrap_all{margin-top: 200px}
    .wrap_all h3,.wrap_all p{ text-align: center}
    .login-wrap{text-align:center;}
    .wrap_all input{display:block; width:250px; height:40px; line-height:40px; margin:0 auto; margin-bottom: 10px; outline:none; border:1px solid #888; padding:10px; box-sizing:border-box;}
    .wrap_all button{display:block; width:250px; height:40px; line-height: 40px; margin:0 auto; border:none; background-color:#41b883; color:#fff; font-size:16px; margin-bottom:5px;}
    .wrap_all span{cursor:pointer;}
    .wrap_all span:hover{color:#41b883;}
</style>

<script>
    export default{
        data(){
            return{
                showLogin: true,
                showRegister: false,
                showTishi: false,
                useDrname: '',
                password: '',
                newUsername: '',
                newPassword: '',
                username:''
            }
        },
        created:function(){
            let username='';
            //如果有session（name）不为空，为session.name赋值 
            if(sessionStorage.getItem('username'))
                  {
                        username =sessionStorage.getItem('username');
                  }
        
            console.log(username);
                  this.username = username;
                  if(this.username == ""){
                      this.$router.push('/login');
                  }else{
                      this.$router.push('/content'); // 页面加载时跳转
                  }
        },
        methods: 
         {
            ToRegister(){
                var _this=this;
                _this.showLogin=false;
                _this.showRegister=true;
            },
            register(){
                if(this.newUsername == "" || this.newPassword == ""){
                    alert("请输入用户名或密码")
                }else{
                    let data = {'user_name':this.newUsername,'password':this.newPassword};
                    this.$axios({
                        method:'post',
                        url:this.url()+'/index.php/adminuser/register',
                        params :{
                        data:data
                        }
                    }).then(res=>{
                        console.log(res);
                       
                        if(res.data.data==-1){
                             alert("用户名已被注册！");
                        }
                        if(res.data.data==1){
                           setTimeout(function(){
                                this.showRegister = false
                                this.showLogin = true
                            }.bind(this),1000);
                        }
                        if(res.data.data==0){
                            _this.showLogin=false;
                            _this.showRegister=true;
                        }
                        

                    });     
                    
                     
                }
            },
            ToLogin(){
                var _this=this;
                _this.showLogin=true;
                _this.showRegister=false;

             },
            login(){
                    if(this.username == "" || this.password == ""){
                        alert("请输入用户名或密码")
                    }else{
                        let data = {'user_name':this.username,'password':this.password}
                        /*接口请求*/
                        this.$axios({
                            method:'post',
                            url:this.url()+'/index.php/adminuser/login',
                            params :{
                            data:data
                            }
                        }).then(res=>{
                            console.log(res);
                            /*接口的传值是(-1,该用户不存在),(0,密码错误)，同时还会检测管理员账号的值*/
                            if(res.data.data == -1){
                                alert('用户名或密码错误！请重新输入');
                            }else if(res.data.data.admin == 'admin'){
                               //alert(res.data.data.user_name);
                                sessionStorage.setItem('username', res.data.data.user_name)
                               // setCookie('username',res.data.data.user_name,1000*60);
                                setTimeout(function(){
                                  //  alert(22);
                                    this.$router.push({path: '/content', query: {username: res.data.data.user_name }});
                                    //this.$router.push('/content');
                                }.bind(this),1000)
                            }
                            
                        }); 
                }
            }
            
         }
       
    }
</script>


